html {
  scroll-behavior: smooth;
}


body{
  margin: 0;
  padding:0;
  
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #18a32b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: 'Ubuntu';
}

.App-link {
  color: #61dafb;
}

.btnWelcome {
  padding: 10px 20px;
  background-color: #ffffff;
  color:rgb(0, 0, 0);
  border-radius: 10px;
  border-style: none;
  transition: .3s ease-in-out;

}

.btnWelcome:hover{
  background-color: aliceblue;
  color:#282c34;
  cursor: pointer;
}

.card {
  margin:1rem;
  text-align: center;
}

.carousel-item {
  width:100%;
  height:600px;
  position: inherit;
  margin-top:.5rem;
}




.navbar-brand {
  font-family: 'Ubuntu';
  font-weight: bold;
  font-size:2rem;
}

.text-ubuntu {
  font-family: 'Ubuntu';
  font-weight: bold;
  font-size: 700;
}


#about-slide {
  position: absolute;
  /* right:10px; */
  left:50%;
  top:50%;
  margin-bottom: 505px;
  color:#ffffff;
  text-shadow: 2px 4px 6px #000;
  text-align: center;
}

#carousel-item2 {
  background-image: url('https://images.pexels.com/photos/4050315/pexels-photo-4050315.jpeg?cs=srgb&dl=pexels-vlada-karpovich-4050315.jpg&fm=jpg');
  background-attachment: fixed;
  background-size:cover;
}
#carousel-item3 {
  background-image: url('https://images.pexels.com/photos/302769/pexels-photo-302769.jpeg?cs=srgb&dl=pexels-pixabay-302769.jpg&fm=jpg');
  background-attachment: fixed;
  background-size:cover;
  
}

#contact {
  padding:7% 15%;
  color:#fff;
  background-image: url('./images/contactus.jpg');
  background-size: cover;
  background-attachment: fixed;
  
}

#contact img {
  width:50%;
  height:auto;
  border-radius: 10px;
  /* padding:2rem; */
  margin:4rem;
}

#footer {
  padding:5%;
  background:#000;
}

#footer ul li {
  list-style: none;
  padding:0px;
}

#services {
  padding:10%;
  
  
}

#small-logo{
  display: flex;
  width:50px;
  align-items: center;
  justify-content: center;
}


li {
  list-style: none;
}

p,li {
  font-family:'Montserrat','Prompt',sans-serif;
}

h1,h2,h3,h4,h5 {
  font-family: 'Ubuntu';
  font-weight: bold;
}

 #carousel-item3 .container {
  position: inherit;
  top:40%;
  color:#fff;
  font-family: 'Ubuntu';
  justify-content: center;
  text-shadow: 2px 4px 6px #000;
  
}



#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
}

#title {
  /* background-image: url('https://images.pexels.com/photos/1921326/pexels-photo-1921326.jpeg?cs=srgb&dl=pexels-markus-spiske-1921326.jpg&fm=jpg'); */
  /* background-attachment: cover; */
}

#title h1{
  font-family: 'Ubuntu';
  font-weight: 700;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media screen and (max-width: 900px) {
  #contact img {
    width: 75%;
  }
  #about-slide {
    position: absolute;
  /* right:10px; */
  left:0;
  top:50%;
  margin-bottom: 505px;
  color:#ffffff;
  text-shadow: 2px 4px 6px #000;
  text-align: center;
  }
}